* {
  box-sizing: border-box;
}

a { 
  text-decoration: none;
}

body {
  padding: 0;
  margin: 0;
  width: 100vw;
}

#root {
  width: 100vw;
  position: relative;
}

.page-wrapper {
  position: relative;
  width: 100vw;
  margin: 0;
}

.page {
  position: relative;
  width: 100vw;
  height: 100vh;
  margin: 0;
}

h2 {
  margin: 0;
  padding: 20px;
}
