.page-navbar-bottom {
    width: 100%;
    /* display: flex;
    flex-direction: row; */
    flex-wrap: wrap;
    vertical-align: bottom;
    position: absolute;
    text-align: center;
}

.page-navbar-bottom > span {
    /* flex: auto;  */
    text-align: center;
    text-decoration: none;
    cursor: pointer;
    margin: 20px;
}
a {
    position: relative;
}
.page-navbar-bottom > span:hover > a::after {
    content: '';
    width: 100%;
    height: 2px;
    background: black;
    position: absolute;
    bottom: -10px;
    left: 0;
}